@import 'reset'; // Сброс стилей
@import 'setting'; // Общие настройки


// Компоненты 
@import 'components/footer';
@import 'components/header';

body,
*,
input,
textarea,
a,
button {
    font-family: 'Ubuntu', sans-serif;
}


* {
    transition: 0.2s linear;
}

// Стили

body.menu-visible, html.menu-visible{
    overflow-y: hidden;
    .nav{
        left: 0 !important; 
    }
}

.button {
    display: block;
    width: max-content;
    padding: 20px 30px;
    background: #FFAE50;
    font-weight: 400;
    font-size: 16px;
    line-height: 100%;
    color: #000000;
    text-align: center;
    border: 2px solid transparent;
    position: relative;
    overflow: hidden;

    &::before {
        content: '';
        display: block;
        position: absolute;
        width: 30px;
        height: 500%;
        background: #fff;
        opacity: 0.3;
        transform: rotate(60deg);
        left: -300px;
        top: -100px;
        transition: 0.4s linear;
    }

    &:hover,
    &:focus {
        &::before {
            left: 300px;
        }

        opacity: 0.9;
    }
}

.header {
    position: absolute;
    width: 100%;
    top: 0;
    z-index: 2;
    border: 1px solid rgba(255, 255, 255, 0.08);



    .top-line {

        @media(max-width: 991px) {
            display: none;
        }

        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 25px 0px;

        &__link {
            display: flex;
            align-items: center;
            font-weight: 400;
            font-size: 14px;
            line-height: 16px;
            color: #FFFFFF;

            &:hover,
            &:focus {
                opacity: 0.6;
                cursor: pointer;
            }

            img {
                margin-right: 18px;
            }

            &+a {
                margin-left: 32px;
            }
        }

        &__contact {
            display: flex;
            align-items: center;
        }
    }

    .bottom-line {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        padding-top: 55px;

        @media(max-width: 991px) {
            align-items: center;
            padding: 25px 0px;
        }

        .logo {
            max-width: 245px;
            width: 100%;

            img {
                width: 100%;
                object-fit: cover;
            }

            @media(max-width: 768px) {
                max-width: 160px;
            }
        }

        .nav {
            display: flex;

            @media(max-width: 991px) {
                position: absolute;
                top: 90px;
                background: #4B4B4B;
                flex-direction: column;
                align-items: center;
                text-align: center;
                width: 100%;
                padding: 50px;
                left: -100%;
            }

            a {
                font-weight: 700;
                font-size: 16px;
                line-height: 150%;
                text-transform: uppercase;
                color: #FFFFFF;
                @media(max-width: 991px) {
                    font-weight: 500;
                    font-size: 18px;
                    line-height: 150%;
                    color: #FFFFFF;
                    text-transform: none;
                }
            }

            li {
                &:not(:last-child) {
                    margin-right: 35px;
                    @media(max-width: 991px) {
                        margin-right: 0;
                        margin-bottom: 15px;
                    }
                }


                position: relative;

                &::after {
                    content: '';
                    position: absolute;
                    height: 2px;
                    width: 100%;
                    background: #FFAE50;
                    bottom: -8px;
                    left: 50%;
                    transform: translateX(-50%);
                    transition: 0.2s linear;
                    opacity: 0;
                }

                &:hover,
                &:focus {
                    &::after {
                        opacity: 1;
                    }

                    a {
                        color: #FFAE50;
                    }

                    @media(max-width: 991px) {
                        &::after{
                            opacity: 0;
                        }
                    }
                }
            }
        }
    }
}


.burger-menu {


    width: 20px;
    height: 12px;
    position: relative;
    padding: 0;

    @media(min-width: 991px) {
        display: none;
    }

    span {
        transition: 0.2s linear;

        &:nth-child(1),
        &:nth-child(2),
        &:nth-child(3) {
            display: block;
            height: 2px;
            background: #fff;
            width: 100%;
            position: absolute;
            border-radius: 2px;
        }

        &:nth-child(1) {
            transform: translateY(-6px);
        }

        &:nth-child(3) {
            transform: translateY(6px);
        }
    }

}

.burger-menu.opened {
    span {
        &:nth-child(2) {
            width: 0;
        }

        &:nth-child(1) {
            transform: translate(0) rotate(45deg);
        }

        &:nth-child(3) {
            transform: translate(0) rotate(-45deg);
        }
    }


}




.main-screen {
    height: 100vh;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    min-height: 800px;

    @media (max-width: 991px) {
        min-height: 0;
        height: auto;
        padding-bottom: 95px;
    }



    &__inner {
        padding-top: 245px;
        position: relative;
        z-index: 1;

        @media(max-width: 768px) {
            padding-top: 140px;
        }

        .points {
            .point {
                display: flex;
                align-items: center;
                font-weight: 400;
                font-size: 16px;
                line-height: 150%;
                color: #F7F7F7;
                position: absolute;

                &:nth-child(1) {
                    right: 100px;
                    top: 270px;
                }

                &:nth-child(2) {
                    right: 0;
                    bottom: 150px;
                }

                &:nth-child(3) {
                    right: 195px;
                    bottom: 50px;
                }

                &:nth-child(4) {
                    left: 50%;
                    transform: translateX(-50%);
                    bottom: -50px;
                }

                img {
                    margin-right: 8px;
                }
            }

            @media(max-width: 991px) {
                display: none;
            }
        }

        h1 {
            font-weight: 500;
            font-size: 48px;
            line-height: 120%;
            color: #FFFFFF;
            max-width: 770px;
            border-left: 15px solid #FFAE50;
            padding-left: 65px;
            padding-top: 65px;
            padding-bottom: 65px;
            position: relative;

            @media(max-width: 768px) {
                font-size: 24px;
                padding: 30px;
                padding-right: 0;
                line-height: 150%;
            }

            &::before,
            &::after {
                content: '';
                display: block;
                position: absolute;
                height: 15px;
                background: #FFAE50;
            }

            &:before {
                top: 0;
                left: 0;
                width: 150px;
            }

            &::after {
                bottom: 0;
                left: 0;
                width: 250px;
            }
        }

        p {
            margin-left: 80px;
            font-weight: 400;
            font-size: 16px;
            line-height: 150%;
            color: #FFFFFF;
            max-width: 430px;
            margin-top: 30px;

            @media(max-width: 768px) {
                margin-left: 0;
            }
        }

        .button {
            margin-left: 80px;
            margin-top: 35px;

            @media(max-width: 768px) {
                margin-left: 0;
            }
        }
    }
}


@media(min-width: 768px){
    .desktop-hidden{
        display: none;
    }
}

.tiles {
    margin-top: -30px;

    &__grid {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
        grid-auto-flow: row;
        grid-gap: 34px;
        align-items: center;

        @media(max-width: 1200px) {
            grid-gap: 20px;
        }

        @media(max-width: 991px) {
            grid-gap: 10px;
        }

        @media(max-width: 768px) {
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 10px;
        }

        &-item {
            position: relative;
            &::before{
                content: '';
                position: absolute;
                width: 8px;
                height: 8px;
                background: #FFAE50;
                top: 16px;
                left: 16px;
                @media(max-width: 768px) {
                    top: 6px;
                    left: 6px;
                    width: 5px;
                    height: 5px;
                }
            }
            @media(max-width: 768px) {
                min-height: 135px;
                max-height: 150px;
                height: 100%;
                padding: 15px;
            }

            padding: 32px;
            background: #1A1A1A;
            color: #fff;
            min-height: 280px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            h3 {
                font-weight: 500;
                font-size: 24px;
                line-height: 120%;
                color: #FFFFFF;

                @media(max-width: 768px) {
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 150%;
                    color: #FFFFFF;
                }
            }

            &:hover {
                background: #FFAE50;
                &::before{
                    background: #1A1A1A;
                }
                .tiles__item-title {
                    color: #1A1A1A;
                }

                .tiles__item-btn {
                    color: #1A1A1A;

                    svg {
                        path {
                            stroke: #1A1A1A;
                        }
                    }


                }

                .tiles__item-icon {
                    svg {
                        path {
                            fill: #1A1A1A;
                            stroke: #1A1A1A;
                        }
                    }
                }
            }
        }




    }


    &__item {
        &-footer {
            display: flex;
            justify-content: space-between;
            align-items: flex-end;

            @media(max-width: 768px) {
                justify-content: flex-end;
            }
        }

        &-icon {
            width: max-content;

            @media(max-width: 768px) {
                width: 32px;
                height: 32px;

                svg {
                    width: 32px;
                    height: 32px;
                }
            }
        }

        &-btn {
            display: flex;
            align-items: center;
            font-weight: 400;
            font-size: 16px;
            line-height: 16px;
            text-transform: uppercase;
            color: #FFFFFF;

            @media(max-width: 768px) {
                display: none;
            }

            svg {
                margin-left: 8px;
            }
        }
    }

}





.gallery{
    margin-top: 130px;
    @media(max-width: 768px){
        margin-top: 50px;
    }
    &__inner{
        position: relative;

    }

    .swiper-pagination{
        bottom: -45px;
        .swiper-pagination-bullet-active{
            background: #FFAE50;
        }

        @media(max-width: 540px){
            bottom: -35px;
        }
    }
    &-swiper{
        height: 320px;
        margin-bottom: 80px;
        @media(max-width: 540px){
            height: 190px;
            width: 250px;
        }
        &-wrapper{
            display: flex;
            height: 100%;
        }

        &-slide{
            flex-shrink: 0;
            height: 100%;
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}


h2.title{
    font-weight: 500;
    font-size: 48px;
    line-height: 120%;
    padding-left: 55px;
    position: relative;
    margin-top: 120px;
    margin-bottom: 45px;
    @media(max-width: 768px){
        font-size: 24px;
        margin-bottom: 25px;
    }
    &::before{
        content: '';
        position: absolute;
        background-image: url("data:image/svg+xml,%3Csvg width='48' height='48' viewBox='0 0 48 48' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M-2.5034e-06 10L48 10' stroke='black'/%3E%3Cpath d='M10 -2.5034e-06L10 48' stroke='black'/%3E%3Crect x='24' y='23' width='16' height='16' fill='%23FFAE50'/%3E%3C/svg%3E%0A");
        width: 48px;
        height: 48px;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        left: 0;
        top: -15px;
        @media(max-width: 768px){
            width: 32px;
            height: 32px;
        }
    }
}



.scheme{
    &__inner{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(auto-fit, 1fr);
        @media(max-width: 991px){
            grid-template-columns: 1fr 1fr;
            grid-gap: 25px;
        }

        @media(max-width: 768px){
            grid-gap: 15px;
        }
    }

    &__item{
       min-height: 300px;
       padding: 24px;
       background: #F1F1F1;
       @media(max-width: 768px){
        min-height: 150px;
            &:nth-child(2),&:nth-child(3),&:nth-child(6){
                background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), #000000;
                color: #fff;
            }
            &:nth-child(1),&:nth-child(4),&:nth-child(5){
                background: #F1F1F1 !important;
                color: #000000 !important;
            }
        }
       &:nth-child(2n+2){
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), #000000;
        color: #fff;
       }
       &-title{
        font-weight: 500;
        font-size: 48px;
        line-height: 120%;
        @media(max-width: 768px){
            font-size: 24px;
        }
       }
       &-text{
        font-weight: 300;
        font-size: 24px;
        line-height: 150%;
        margin-top: 15px;
        @media(max-width: 768px){
            font-size: 16px;
        }
       }
    }
}



.steps-form{
    padding: 60px 0px;
    margin-top: 60px;
    background: #F1F1F1;
    .title.centered{
        width: max-content;
        @media(max-width: 768px){
            width: auto;
        }
        max-width: 800px;
        margin: 0 auto;
    }

    .steps-form__inner{
        max-width: 800px;
        background: #fff;
        width: 100%;
        margin: 0 auto;
        margin-top: 40px;
        form{
            display: flex;
            height: 100%;
            min-height: 550px;
            @media(max-width: 768px){
                min-height: 0;
            }
        }
        .form-aside{
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            background: #FAFAFA;
            padding-left: 40px;
            padding-top: 40px;
            padding-bottom: 60px;
            min-height: 550px;
            padding-right: 15px;
            min-width: 175px;
            @media(max-width: 768px){
                display: none;
            }
            .step-btn{
                position: relative;

                    &::before{
                        content: '';
                        position: absolute;
                        display: block;
                        width: 35px;
                        height: 35px;
                        background: #FFAE50;
                        border-radius: 100%;
                        z-index: 1;
                        left: 15px;
                        top: 35px;
                        opacity: 0;
                        transition: 0.2s linear;
                    }

                    &:nth-child(3){
                        &::before{
                            top: 30px;
                        }
                    }
                text-align: left;
                flex: 1;
                cursor: pointer;
                svg{
                    opacity: 0.08;
                   position: relative;
                   z-index: 5;
                }
                &:hover{
                    span{
                        color: #000000;
                    }
                    svg{
                        
                        opacity: 1;
                    }
                    &::before{
                        opacity: 1;
                    }
                }
                span{
                    display: block;
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 16px;
                    color: rgba(0, 0, 0, 0.32);
                    margin-top: 15px;
                }
            }

            .step-btn.active{
                span{
                    color: #000000;
                }
                svg{
                    
                    opacity: 1;
                }
                &::before{
                    opacity: 1;
                }
            }

        }

        .form-steps{
            padding: 40px 85px;
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding-bottom: 100px;
            @media(max-width: 768px){
                padding: 30px;
            }
            .step-title{
                font-weight: 500;
                font-size: 24px;
                line-height: 120%;
                margin-bottom: 8px;
                
            }
            .form-title{
                font-weight: 400;
                font-size: 16px;
                line-height: 150%;  
                opacity: 0.4;
            }
            .inputs-wrapper{
                margin-top: 35px;
                display: flex;
                flex-direction: column;
                label{
                    margin-bottom: 32px;
                    position: relative;
                    input{
                        width: 100%;
                    }
                    position: relative;
                    span{
                        position: absolute;
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 16px;
                        color: #00000060;
                        background: #fff;
                        padding: 0px 4px;
                        top: -8px;
                        left: 15px;
                        z-index: 5;
                    }
                }

                .choices{
