                            left: -6px;
                        }
                    }
                }
            }

            .form-buttons{
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: flex-end;
                @media(max-width: 768px){
                    flex-direction: column;
                    align-items: center;
                }
                .next{
                    padding: 20px 75px;
                    cursor: pointer;
                    @media(max-width: 768px){
                        margin-bottom: 25px;
                    }
                }
                .prev{
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 16px;
                    opacity: 0.4;
                    cursor: pointer;
                    &:hover{
                        opacity: 1;
                    }
                }
            }
        }
    }
}

